<template>
    <div class="geographie">
        <h1>Géographie</h1>
        La géographie de la France consiste en l'étude du territoire de la France, pays d'une superficie de 551 695 km2
        (région
        métropolitaine) et 675 417 km2 avec l'outre-mer, s'étendant sur 1 000 km du nord au sud et d'est en ouest (plus
        grande
        distance du nord au sud : Bray-Dunes à Cerbère).
    </div>
</template> 
 
<script>
export default {
    name: 'geographie',
    metaInfo: {
        title: 'Geographie de la France',
        meta: [{
            name: 'description',
            content: 'Découvrez un paysage époustouflant aux multiples environnement'
        }]
    }
} 
</script> 


