<template>
    <div class="culture">
        <h1>Culture</h1>
        Sa culture officielle est relayée par les médias nationaux. Caractérisée par un art de vivre, elle maintient des
        valeurs traditionnelles, parmi lesquelles une gastronomie reconnue, marquée notamment par ses fromages et ses
        vins, et des domaines comme la haute couture et la haute joaillerie.
    </div>
</template> 
 
<script>
export default {
    name: 'culture',
    metaInfo: {
        title: 'Culture de la France',
        meta: [{
            name: 'description',
            content: 'Art, litterature, cuisine et philosophie ...'
        }]
    }
} 
</script> 


