<template>
    <div class="histoire">
        <h1>Histoire</h1>
        L'histoire de la France commence avec les premières occupations humaines du territoire correspondant au pays
        actuel. Aux
        groupes présents depuis le Paléolithique et le Néolithique, sont venues s'ajouter, à l'Âge du bronze et à l'Âge
        du fer,
        des vagues successives de Celtes, puis au iiie siècle de peuples germains (Francs, Wisigoths, Alamans,
        Burgondes) et au
        ixe siècle de scandinaves appelés Normands.
    </div>
</template> 
 
<script>
export default {
    name: 'histoire',
    metaInfo: {
        title: 'L histoire de France',
        meta: [{
            name: 'description',
            content: 'Une histoire riche et mouvementé, découvrez comment la France est devenue ce qu elle est aujourdhui'
        }]
    }
} 
</script> 


